.hot-prices {
  padding: 82px $contentHorPadding1440px 0;

  &__control {
    display: flex;
    justify-content: space-between;
  }

  &__wrapper {
    display: flex;
  }
  &__button-arrow {
    &--back {
      margin-right: 16px;
      width: 32px;
      height: 32px;
    }

    &--next {
      width: 32px;
      height: 32px;
    }
  }
}
